import { Route, Navigate } from "react-router-dom";
import  useGlobalContext from "../hooks/useGlobalHook";


export default function PrivateRoute({ children, ...rest }) {
  let auth = useGlobalContext()

  return (
    <Route
      {...rest}
      render={({ location }) =>
        auth.user ? (
          children
        ) : (
          <Navigate
            to={{
              pathname: "/login",
              state: { from: location }
            }}
          />
        )
      }
    />
  );
}
